          <van-tabbar-item icon="apps-o" to="/catelist">分类</van-tabbar-item>
          <van-tabbar-item icon="shopping-cart-o" to="/car">购物车</van-tabbar-item>
          <van-tabbar-item icon="manager-o" to="/mine">我的</van-tabbar-item>
