<template>
    <div>
    <!-- header -->
    <van-sticky>
        <header>
            <div class="backbox"  @click="backbtn">
                <van-icon name="arrow-left" />
            </div>
            <div class="searchbox">
                <van-search v-model="value" placeholder="请输入搜索关键词" class="headersearch" shape="round"/>
            </div>
            <div class="morebox">
                <van-icon name="ellipsis" />
            </div>
    </header>
    </van-sticky>
    <!-- mian -->
    <div style="position: relative;">
        <van-sidebar v-model="activeKey">
          <van-sidebar-item v-for="(n,inx) in titlelist" :key="inx"     :title="n.title" @click="pageshow(inx)"/>
        </van-sidebar>
        <div class="goodshowbox" v-show="flag0">
            <div class="toptitle">
                <span>热门分类</span>
                <a href="javascript:;">
                    <van-icon name="bar-chart-o" />
                    排行榜
                    <van-icon name="arrow" />
                </a>
            </div>
            <div class="goodshowlistbox">
                <van-grid :border="false" :column-num="3">
                    <van-grid-item v-for="(n,inx) in rememendlist" :key="inx" class="goodshow">
                      <van-image :src="n.src" />
                      <span class="goodtxt">{{n.txt}}</span>
                    </van-grid-item>
                </van-grid>
            </div>
            <div class="toptitle">
                <span>家电热搜</span>
            </div>
            <div class="goodshowlistbox">
                <van-grid :border="false" :column-num="3">
                    <van-grid-item v-for="(n,inx) in appliancelist" :key="inx">
                      <van-image :src="n.src" />
                      <span class="goodtxt">{{n.txt}}</span>
                    </van-grid-item>
                </van-grid>
            </div>
        </div>
        <div class="goodshowbox" v-show="flag1">
            <h1>这是手机数码的页面</h1>
        </div>
    </div>
    <!--  footer -->
    <footer_bar></footer_bar>
    </div>
</template>

<script>
import footer_bar from './footer_bar'
export default {
    name:'catelist',
    components:{footer_bar},
    data(){
        return{
            active: 0,
            value: '',
            activeKey: '',
            titlelist:[{
                title:"热门推荐"
            },
            {
                title:"手机数码"
            },
            {
                title:"家用电器"
            },
            {
                title:"电脑办公"
            },
            {
                title:"美妆护肤"
            },
            {
                title:"个护清洁"
            },
            {
                title:"汽车生活"
            },
            {
                title:"京东超市"
            },
            {
                title:"男装"
            },
            {
                title:"女装"
            },
            {
                title:"男鞋"
            },
            {
                title:"女鞋"
            }],
            rememendlist: [{
                src:'https://img14.360buyimg.com/focus/s140x140_jfs/t27136/183/1628977274/31007/a6f7ed55/5be6ebd8Nb07ef492.png',
                txt:'手机'
            },{
                src:'https://img20.360buyimg.com/focus/s140x140_jfs/t1/86638/37/11918/19572/5e3e4307E7606f742/0210ac72c0d9275e.jpg',
                txt:'全面屏手机'
            },{
                src:'https://img20.360buyimg.com/focus/s140x140_jfs/t1/102972/19/11837/14987/5e3d8bdaEc8ffe21e/6119d3f773fcdacd.jpg',
                txt:'游戏手机'
            },{
                src:'https://img10.360buyimg.com/focus/s140x140_jfs/t1/106781/8/11928/23561/5e3e833fE92c281f5/72b834ead18b6462.jpg',
                txt:'充电宝'
            },{
                src:'https://img12.360buyimg.com/focus/s140x140_jfs/t1/96627/6/11719/15770/5e3e83b6E05b80eea/76b39555bc2d2a89.jpg',
                txt:'体温计'
            },{
                src:'https://img11.360buyimg.com/focus/s140x140_jfs/t1/99797/24/11893/15941/5e3e841aE2b32361d/63dcf9162ec4e1f4.jpg',
                txt:'中医保健'
            },{
                src:'https://img13.360buyimg.com/focus/s140x140_jfs/t1/109041/23/5605/14288/5e3e848bE289e1d01/8b8af78cfeda0e5e.jpg',
                txt:'调节免疫'
            },{
                src:'https://img14.360buyimg.com/focus/s140x140_jfs/t1/108873/12/5675/17296/5e3e8517E22c8d9ad/b53ebc459d1b69fe.jpg',
                txt:'经管励志'
            },{
                src:'https://img11.360buyimg.com/focus/s140x140_jfs/t1/97873/26/11984/16170/5e3e878eE3d6f29d8/820c61d6be7faf12.jpg',
                txt:'投影机'
            },{
                src:'https://img20.360buyimg.com/focus/s140x140_jfs/t1/96008/21/11832/14563/5e3e883fE941b27f7/e073dd6dd02ecd02.jpg',
                txt:'游戏机'
            }],
            appliancelist: [{
                src:'https://img20.360buyimg.com/focus/s140x140_jfs/t21115/83/225125274/13856/5473fb3f/5b0567c1N59d53b27.png',
                txt:'冰箱'
            },{
                src:'https://img30.360buyimg.com/focus/s140x140_jfs/t1/103763/18/11682/15934/5e3e8113E0ec7622d/60e28dbaee663653.jpg',
                txt:'电磁炉'
            },{
                src:'https://img13.360buyimg.com/focus/s140x140_jfs/t1/89471/40/11805/13074/5e3e8194E4f565ca8/62a310314e34dcf2.jpg',
                txt:'微波炉'
            },{
                src:'https://img10.360buyimg.com/focus/s140x140_jfs/t1/99259/34/11845/15840/5e3e8258Ea2ebb9dd/50154478628700f6.jpg',
                txt:'曲面电视'
            },{
                src:'https://img30.360buyimg.com/focus/s140x140_jfs/t1/102684/5/11696/17388/5e3e85d5E865de0cc/f6c53a8ec210a44c.jpg',
                txt:'电热饭盒'
            }],
            flag0: true,
            flag1: false,
        }
    },
    methods:{
        pageshow: function (inx) {
            if(inx===0){
                this.flag0 = true;
                this.flag1 = false;
            }else if(inx===1){
                this.flag1 = true;
                this.flag0 = false;
            }
        },
        backbtn:function(){
            history.go(-1);
        }
    }
}
</script>

<style scoped>
header{
    display: flex;
    height: .4rem;
    width: 100%;
    border-bottom: 1px solid #ccc;
    background-color: #fff;
}
.backbox{
    flex: 1;
    text-align: center;
    margin-top: .05rem;
}
.searchbox{
    flex: 8;
    background-color: rgb(109, 79, 79);
}
.morebox{
    flex: 1;
    text-align: center;
    margin-top: .05rem;
}
.headersearch{
    height: .4rem;
}
.goodshowbox{
    height: 7rem;
    width: 2.9rem;
    position: absolute;
    top: 0;
    right: 0;
}
.toptitle{
    position: relative;
}
.toptitle>span{
    font-size: .2rem;
}
.toptitle>a{
    display: block;
    font-size: .14rem;
    position: absolute;
    right: 0;
    top: .1rem;
}
.goodshow{
    height: 1.2rem;
}
.goodtxt{
    font-size: .14rem;
    display: block;
    margin-top: .1rem
}
</style>


