// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import './assets/rem'
import './assets/reset.css'
import Vue from 'vue'
import App from './App'
import router from './router'
import { Button, Col, Row, Search, Swipe, SwipeItem, Grid, GridItem
        ,Sticky ,Tab, Tabs, CountDown , Icon,Card,Tabbar, TabbarItem,Sidebar, SidebarItem ,Image,ActionSheet,Radio ,RadioGroup,Tag,Stepper,SubmitBar,Sku,Popup,Cell,AddressList,Rate} from 'vant'

Vue.use(Button).use(Col).use(Row).use(Search).use(Swipe)
   .use(SwipeItem).use(Grid).use(GridItem).use(Sticky).use(Tab)
   .use(Tabs).use(CountDown).use(Icon).use(Card).use(Tabbar)
   .use(TabbarItem).use(Sidebar).use(SidebarItem).use(Image)
   .use(ActionSheet).use(Radio).use(RadioGroup).use(Tag)
   .use(Stepper).use(SubmitBar).use(Sku).use(Popup).use(Cell)
   .use(AddressList).use(Rate)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



