<template>
    <div>
    <!-- header -->
    <van-sticky>
        <header>
            <div class="backbox" @click="backbtn">
                <van-icon name="arrow-left" />
            </div>
            <div class="searchbox">
                <span class="txtbox">购物车</span>
            </div>
            <div class="morebox">
                <van-icon name="ellipsis" />
            </div>
        </header>
    </van-sticky>
    <!-- location -->
    <div class="locationbox">
        <div>
            <van-icon name="location" style="display:flex;float: left;line-height:.4rem;" color="#ccc"/>
            <div style="display:flex;float: left;color:#ccc">南岸区城区</div>
            <span style="display:flex;float: right;">编辑商品</span>
        </div>
    </div>
    <!-- tips -->
    <div class="tipsbox">
        <div>购物车中有一件降价商品，点击查看商品</div>
        <van-icon name="cross" id="tipdelete" color="#E93B3D" @click="tipdelete"/>
    </div>
    <!-- goodcard -->
    <div>
        <ul>
            <li class="cargood" v-for="(n,inx) in cargoodlist" :key="inx" >
                <div class="cardtop">
                    <input type="checkbox" name="" id="" >
                    <div>
                        <van-icon name="shop-collect-o" style="height:.3rem;line-height:.3rem"/>
                        <div>{{n.shopname}}</div>
                    </div>
                    <span>优惠券</span>
                </div>
                <div class="card">
                    <div>
                        <input type="checkbox" name="" id="">
                    </div>
                    <div>
                        <van-card  :price="n.price" :desc="n.desc" :title="n.title" :thumb="n.imgsrc" class="goodcard">
                          <div slot="tags">
                            <van-tag plain type="danger" class="goodtag">标签</van-tag>
                          </div>
                          <div slot="footer">
                            <van-stepper disable-input v-model="n.count" class="goodcount" @plus="addprice(inx)"
                             @minus="minuprice(inx)"/>
                            <van-button class="similar" size="mini">查看相似</van-button>
                            <van-button class="concern" size="mini">移入关注</van-button>
                            <van-button class="remove" size="mini" @click="gooddelete(inx)">删除</van-button>
                          </div>
                        </van-card>
                    </div>
                </div>
                <div class="between"></div>
            </li>
        </ul>
    </div>
    <!--  footer -->
    <van-sticky>
        <div class="footerbox">
            <div class="pricebox">{{allcount/100}}</div>
            <button @click="onSubmit">去结算</button>
        </div>
    </van-sticky>
    </div>
</template>

<script>
import footer_bar from './footer_bar'
export default {
    name:'car',
    components:{footer_bar},
    data(){
        return{
            active: 0,
            value: '',
            allcount: '31790',
            cargoodlist:[{
                shopname:'京东自营',
                imgsrc:'https://img10.360buyimg.com/mobilecms/s234x234_jfs/t23227/304/2115400247/139827/ca0ef0f6/5b7518c9N1ee4b506.jpg!q70.dpg.webp',
                title:'陕西徐香绿心猕猴桃',
                desc:'22个板盒装 单果90-110g 新鲜水果 年货礼盒',
                price:'49.90',
                count:'1',
            },{
                shopname:'京屋西柚专卖店',
                imgsrc:'https://img10.360buyimg.com/mobilecms/s234x234_jfs/t1/87831/26/3970/437136/5de4c464E0c6bb1c4/3cc4cd717424a99e.jpg!q70.dpg.webp',
                title:'京屋（jingwu）电竞椅',
                desc:'电脑椅游戏椅升降椅子家用办公椅人体工学椅可躺网吧网咖竞技座椅靠背椅转椅子 红黑撞色 尼龙脚',
                price:'268.00',
                count:'1',
            }],
        }
    },
    methods:{
        addprice:function(inx){
            var countprice = parseFloat(this.allcount);
            countprice = countprice + parseFloat(1*(this.cargoodlist[inx].price))*100;
            this.cargoodlist[inx].count++;
            this.allcount = countprice; 
        },
        minuprice:function(inx){
            var countprice = parseFloat(this.allcount);
            countprice = countprice - parseFloat(1*(this.cargoodlist[inx].price))*100;
            this.cargoodlist[inx].count--;
            this.allcount = countprice;
        },
        onSubmit:function(){
            console.log(22);
        },
        backbtn:function(){
            history.go(-1);
        },
        tipdelete:function(){
            var tipbox = document.querySelector('.tipsbox');
            tipbox.style.display = 'none';
        },
        gooddelete:function(inx){
            console.log(inx);
            this.cargoodlist.splice(inx, 1);
        },
    }
}
</script>

<style scoped>
header{
    display: flex;
    height: .4rem;
    width: 100%;
    border-bottom: 1px solid  rgb(230, 230, 255);
    background-color: #fff;
}
.backbox{
    flex: 1;
    text-align: center;
    margin-top: .05rem;
}
.searchbox{
    flex: 8;
}
.morebox{
    flex: 1;
    text-align: center;
    margin-top: .05rem;
}
.txtbox{
    display: block;
    text-align: center;
    font-size: .22rem;
    height: .4rem;
    line-height: .4rem;
}
.locationbox{
    height: .4rem;
    width: 100%;
    font-size: .16rem;
    border-bottom: 1px solid rgb(230, 230, 255);
}
.locationbox>div{
    height: .4rem;
    line-height: .4rem;
    padding-left: .05rem;
    padding-right: .05rem;
}
.tipsbox{
    height: .3rem;
    background-color: #FDF0F0;
    font-size: .12rem;
    padding: 0 .05rem 0 .05rem;
    line-height: .3rem;
}
.tipsbox>div{
    display: flex;
    float: left;
    color: #E93B3D;
}
#tipdelete{
    display: flex;
    float: right;
    height: .3rem;
    line-height: .3rem;
}
.cargood{
    height: 2.4rem;
    padding: .1rem;
    box-sizing: border-box;
    position: relative;
}
.cardtop{
    height: .3rem;
    padding: 0 .1rem 0 .1rem;
    line-height: .3rem;
    font-size: .2rem;
}
.cardtop>input{
    height: .2rem;
    width: .2rem;
    display: flex;
    float: left;
    margin-top: .05rem;
}
.cardtop>div{
    display: flex;
    float: left;
    margin-left: .1rem
}
.cardtop>span{
    display: flex;
    float: right;
    font-size: .14rem;
    color: #F2270C;
}
.card{
    padding-left: .1rem;
}
.card>div>input{
    height: .2rem;
    width: .2rem;
    display: flex;
    float: left;
    height: 1.5rem;
    line-height: 1.5rem;
}
.goodcard{
    display: flex;
    float: left;
    height: 1.5rem;
    background-color: #fff;
    position: relative;
    width: 90%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.similar{
    position: absolute;
    bottom: .35rem;
    right: 0;
}
.concern{
    position: absolute;
    bottom: 0;
    right: .8rem;
}
.remove{
    position: absolute;
    bottom: 0;
    right: 0;
}
.between{
    height: .3rem;
    width: 100%;
    background-color: #F7F7F7;
    position: absolute;
    bottom: 0;
    left: 0;
}
.goodtag{
    position: absolute;
    bottom: .3rem;
}
.goodcount{
    position: absolute;
    right: 0;
    bottom: .7rem;
}
.pricebox::before{
    content: "合计：¥ ";
    color: black;
}
.footerbox{
    padding: .1rem 0 0 1rem;
    position: relative;
}
.pricebox{
    color: red;
    font-size: .2rem;
}
.footerbox>button{
    background-color: #F21D0C;
    padding: .1rem;
    position: absolute;
    top: 0;
    right: 0;
    color: #fff;
    font-size: .22rem;
}
</style>


